/* 
Description:  Main application which reads data from file and validates data
Input : File path
How it Works:
	1. Checks if file exists , 
	2. if yes checks if file extension is correct 
	3. Converts file data to Json array and passes it on to data validation method
	5. Returns appropriate error message if file doesn't exists of if file extension is not correct
*/
var Converter = require('csvtojson').Converter;

var fs = require('fs');

// transactions.ReadAndValidateTransactionData('./input/input1.txt');

var ProcessDataFromFile = {
	ReadAndValidateDataFromFile: function(filePath, fn){
		
		if (fs.existsSync(filePath)){
			//console.log('file exits');
			var fileExtension = filePath.split('.').pop()
			if (fileExtension === 'txt'){
			//		console.log('file extension is correct');
						var converter = new Converter({});
						converter.fromFile(filePath,function(err,results){
							var tranStatus=[];
							for(var i = 0 ; i< results.length;i++){
								//console.log('test');
								//tranStatus.push(transactions.ValidateTransationData(results[i]));
							 }
							fn(tranStatus);
					})
			}else {
				fn('Invalid File Type');
			}

		}else {
			fn('File do not exists');
		}

	}
};
module.exports = ProcessDataFromFile;